<template>
  <div id="app">
    <div class="input-box">
      <input type="text" class="user-name" v-model="name" placeholder="名字" />
      <input
        type="text"
        class="user-voice"
        v-model="voice"
        placeholder="音色"
      />
      <input type="file" class="user-picture" @change="inputPicture" />
      <button @click="generate">生成</button>
    </div>

    <div class="box" ref="target">
      <div class="box-bg"></div>
      <div class="text-name">{{ name }}</div>
      <div class="text-voice">{{ voice }}</div>
      <div
        class="avator"
        :style="{
          backgroundImage: `url(${picture})`,
        }"
      ></div>
    </div>
  </div>
</template>

<script>
import html2canvas from "html2canvas";
export default {
  name: "App",
  data() {
    return {
      voice: "",
      name: "",
      picture: "",
    };
  },
  methods: {
    inputPicture(e) {
      let file = e.target.files[0];
      let reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = (re) => {
        this.picture = re.target.result;
      };
    },
    generate() {
      let target = this.$refs.target;
      html2canvas(target).then((canvas) => {
        let a = document.createElement("a");
        a.href = canvas.toDataURL("image/jpeg", 1);
        a.download = "声鉴图";
        a.click();
      });
    },
  },
  components: {},
};
</script>

